.post{
    width: 100%;
    margin-top: 15px;
    border-radius: 15px;
    background-color: white;
    box-shadow: 0 5px 7px -7px rgba(0,0,0,0.75);
}
.post__top {
    display: flex;
    position: relative;
    align-items: center;
    padding: 15px;
}

.post__avatar{
    margin-right: 10px;
}
.post__topInfo > h3{
    font-size: medium;
}
.post__topInfo > p {
    font-size: small;
    color: gray;
}

.post__bottom{
    margin-top: 10px;
    margin-bottom: 10px;
    padding: 15px 20px;
}
.post__image > img{
    width: 100%;
}
.post__options{
    padding-top: 10px;
    border-top:1px solid lightgray;
    display: flex;
    justify-content: space-evenly;
    font-size: medium;
    color:gray;
    cursor: pointer;
    padding: 15px;
}
.post__option{
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 5px;
    
}
.post__option > p{
    margin-left: 10px;
}
.post__option:hover{
    background-color: #eff2f5;
    border-radius:10px;
}
.post__option__bottom{
    width:100%;
    text-align: center;
    cursor: pointer;
    padding: 15px;
    color:gray;
    border-top:1px solid lightgray;
}
.post__option__bottom:hover{
    background-color: #eff2f5;
    border-radius:10px;
}
@media only screen and (max-width: 600px) {
    .post__options{
        display: list-item;
    }

}